table {
  margin: 20px auto;
  border-spacing: 0px;
  width: 680px;
  box-shadow: 5px 4px 30px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  border-top-left-radius: 10px;
  background: #fff;
  transition: all 0.5s;

  td {
    padding: 20px 15px;
  }

  thead {
    border-radius: 10px 10px 0 0;

    tr {
      border-radius: 10px 10px 0 0;
    }
    th {
      color: grey;
    }

    td {
      color: #fff;
      font-size: 20px;
    }

    td:first-of-type {
      border-radius: 10px 0 0 0;
    }

    td:last-of-type {
      border-radius: 0 10px 0 0;
    }
  }

  tbody {
    td {
      border-top: 1px solid #f0f0f0;
    }

    tr:hover {
      background: rgba(123, 153, 218, 0.1);
    }
  }
}
div.columnsearch {
  padding-top: 100px;
}
table.threeDMode {
  transform: rotateX(65deg) rotateZ(-35deg);
}

button {
  border: none;
  background: #000;
  color: #fff;
  padding: 10px 50px;
  font-family: "Red Hat Display", sans-serif;
  font-weight: 600;
  margin: 0 auto;
  display: block;
}
