.pageheader {
  align-items: center;
  background-color: var(--bg-primary);
  border-bottom: var(--border) solid var(--border-color);
  box-shadow: 0 1px 9px 1px rgba(0, 0, 0, 0.35);
  grid-area: 1/1/2/2;
  justify-content: space-between;
  z-index: 100;
}
.titlebar {
  align-items: center;
  text-align: center;
  font-size: larger;
  width: auto;
  display: block;
}
.nav {
  display: flex;
  flex-flow: wrap;
  margin: 0 auto;
  width: auto;
  height: 25px;
  padding-bottom: 25px;
  #border: red 10px solid;
}
.nav_menuitem {
  color: #111;
  flex: 1 1 auto;
  font-size: 0.75rem;
  text-align: center;
  padding: 6px 8px;
}
